
@font-face {
  font-family: 'Roboto';
  src: url("/static/fonts/Roboto-Regular.ttf");
}
body {
  margin: 0;
  font-family: 'Roboto';
}
iframe {
  display: block;
  margin: 0 auto;
}
a {
  color: #00c;
  text-decoration: none;
}
a:hover {
    color: #e61400;
}
.schedule {
  width: 90%;
  margin: 20px auto;
  table-layout: fixed;
  border-collapse: collapse;
  border-spacing: 0;
}
.schedule td {
    text-overflow: ellipsis;
    padding: 10px 15px;
    border-bottom: 1px solid #CCCCCC;
}
.school-list {
  color: #BDBDBD;
  font-size: 10px;
}
.workplace {
  color: #BDBDBD;
  margin-left: 3px;
}
.filter {
  width: 830px;
  margin: 0 auto;
  padding: 20px 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.filter__elem {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-right: 10px;
}
input {
  box-sizing: border-box;
  font-size: 14px;
  width: 100%;
  border: 1px solid #BDBDBD;
  padding: .4em 10px;
  outline: 0;
  border-radius: 0;
  -webkit-tap-highlight-color: transparent;
  -webkit-appearance: none;
}
input:focus {
    border-color: #d0ba65;
    box-shadow: 0 0 10px #fc0;
}
.select-wrapper {
  padding: 0;
  margin: 0;
  padding: 3px 10px;
  border: 1px solid #BDBDBD;
  overflow: hidden;
  background-color: #fff;
  background: url("/static/img/dropdown.svg") no-repeat 98% 50%;
}
select {
  font-size: 14px;
  padding-bottom: 2px;
  width: 130%;
  border: none;
  box-shadow: none;
  background-color: transparent;
  background-image: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
select:focus {
    outline: none;
}
.datepicker .calendar .cell:not(.blank):not(.disabled).day:hover {
  border-color: #ffdb4d;
}
.datepicker .calendar .cell.selected {
  background-color: #ffdb4d;
  color: white;
}
@-webkit-keyframes spin {
100% {
    -webkit-transform: rotate(360deg);
}
}
@keyframes spin {
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
.loading {
  display: inline-block;
  width: 50px;
  height: 50px;
  -webkit-animation-name: spin;
  animation-name: spin;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.loading-wrapper {
    text-align: center;
    padding: 120px 0;
}
.loading-inner {
    position: absolute;
    top: 0;
    left: 50%;
    overflow: hidden;
    width: 100%;
    height: 100%;
}
.loading-inner:before {
      position: absolute;
      top: 0;
      left: -50%;
      width: 100%;
      height: 100%;
      content: '';
      border-radius: 100%;
      box-shadow: inset 0 0 0 2px #fc0;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 1000px) {
.filter {
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    display: block;
}
.filter__elem {
      width: 100%;
      margin-bottom: 10px;
}
.schedule {
    font-size: 12px;
    width: auto;
    margin: 0;
}
.workplace {
    display: none;
}
}
@media (max-device-width: 1024px) {
.schedule {
    width: auto;
}
}



