
@font-face {
  font-family: 'Roboto';
  src: url("/static/fonts/Roboto-Regular.ttf");
}
body {
  margin: 0;
  font-family: 'Roboto';
}
iframe {
  display: block;
  margin: 0 auto;
}
a {
  color: #00c;
  text-decoration: none;
}
a:hover {
    color: #e61400;
}
#app {
  min-width: 1000px;
  max-width: 1440px;
}
h1 {
  text-align: center;
  padding: 20px;
  margin: 0;
}
.header {
  width: 100%;
  text-align: center;
  background-color: #e61400;
  color: white;
  padding: 0 100px;
  height: 78px;
  position: relative;
  box-sizing: border-box;
}
.header__go-home {
    color: white;
    text-transform: uppercase;
    opacity: .6;
    float: left;
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
    -webkit-transition: opacity .15s ease-in-out;
    transition: opacity .15s ease-in-out;
}
.header__go-home:hover {
      color: white;
      opacity: 1;
}
.header__go-home--image {
      background: url("/static/img/back.svg");
      width: 30px;
      height: 30px;
      background-size: 30px;
      display: none;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 1024px) {
body {
    overflow: hidden;
    overflow-y: auto;
    font-size: 16px;
}
h1 {
    padding: 20px 22px;
    font-size: 30px;
}
#app {
    min-width: auto;
}
.header {
    padding: 0;
    height: auto;
}
.header__go-home--text {
      display: none;
}
.header__go-home--image {
      display: block;
}
}



